/* Center the login container */
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f5f5f5;
}

/* Style for input fields */
.login-container input {
  width: 80%;
  max-width: 300px;
  padding: 10px;
  margin: 10px 0;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

/* Style for the login button */
.login-container button {
  width: 80%;
  max-width: 300px;
  padding: 10px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.login-container button:hover {
  background-color: #0056b3;
}

/* Navbar styling */
.navbar {
  width: 100%;
  padding: 15px;
  background-color: #333;
  color: white;
  display: flex;
  justify-content: center;
  font-size: 18px;
}

/* Footer styling */
.footerStyle {
  width: 100%;
  padding: 10px;
  text-align: center;
  background-color: #333;
  color: white;
  position: fixed;
  bottom: 0;
  left: 0;
}
